@import url(https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,300;0,400;0,600;1,300&display=swap);
/* import google font "Kanit" */


/* CSS reset */
* {
  font-family: 'Kanit', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


html {
  background-color: #CC0F35!important;
}

/*
  if height value is not set on html and body,
  it will assume the same value given to children
  that is explicitly assigned a value (i.e. div#root)
*/
div#root {
  min-height: 100vh;
  /* min-height: -webkit-fill-available; */
  background-color: #CC0F35;
}

/*  sets sidebar to overlay page main content,
    requires <main> or parent position: relative (!== static) */
.nav-menu {
  position: absolute;
  background-color: #ab1236;
  z-index: 1;
  transition: 850ms;
  padding: 0px .5rem;
  top: calc(48px + 24px); /* parent padding + burger icon height */
  bottom: -32px; /* -height of footer to reach bottom of document */
  left: -100%;
  border-radius: 0px 4px 4px 0px;
  border-color: gold;
  box-shadow: 0 0 .4rem gold;
}

/* slides sidebar to view when active */
.nav-menu.is-active {
  left: 0;
  transition: 350ms;
}

/* highlights active nav item */
li.is-active {
  background-color: #ffffff;
  border-radius: 5px;
}

/* add gap between nav links in sidebar and icreases size of container */
.sidebar-links li {
  margin-top: .5rem;
  padding: .5rem 1rem;
}

/* set nav links font color except for link */
.sidebar-links li a:not(.active-font) {
  color: #ffe08a!important;
}

button {
  background-color: transparent;
  border: none;
}
* {
  box-sizing: border-box;
}

/* prevents embedded map from overflowing container */
iframe {
  width: calc(100%);
}

/* hover effect for menu items */
div.card:hover {
  transform: scale(1.05);
  border-color: gold;
  box-shadow: 0 0 .5rem gold;
}

div.message-body {
  border-width: 3px!important;
}

/* for Link components */
.active-font {
  color: #C79200!important;
}

.is-transparent {
  background-color: transparent!important;
  box-shadow: none!important;
}

.has-text-bgc {
  color: hsl(348, 86%, 43%);
}

/* if display is less than or equal to 1023px (less than desktop) */
@media (max-width: 1023px) {
  /* for sidebar position: absolute to achieve height: 100% */
  main {
    position: relative;
    height: 100%;
    /* fills remaining height of parent; requires parent display: flex */
    flex: 1 1;
  }

  article {
    width: 100%;
    max-width: 512px;
  }

  /* allows <main> to fill remaining space of its parent container */
  div#root {
    display: flex;
    flex-direction: column;
  }

  /* allows contact details and map to wrap on touch devices */
  div.message-body.is-flex {
    flex-wrap: wrap;
  }
  
  .img-container {
    width: 100%;
    height: 100%;
  }
}

/* display is greater than or equal to 1024px (desktop or greater only) */
@media (min-width: 1024px) {
    article {
      width: 50%;
  }
  
  .img-container {
    width: 66%;
    height: 66%;
  }  
}

@media (max-width: 399px) {
  .hero-body {
    padding: 0!important;
  }
}

/*
  keeps footer at the bottom of page
  requires div#root min-height 100vh
*/
div#root > footer {
  position: -webkit-sticky;
  position: sticky;
  top: 100vh;
  background-color: transparent;
  z-index: 2;
}

